* {
    border: 0;
    outline: none;
}
#chat {
    background: #333333;
    width: 400px;
    position: absolute;
    right: 0px;
    top: 0px;
    display: block;
    height: 100vh;
    border-left: 4px solid #000;
    z-index: -1;
}
#messages{
    width: 100%;
    margin: 0;
    position: absolute;
    bottom: 50px;
    display: block;
    padding: 0px;
}
#messages li{
    background: #222;
    color: #fff;
    list-style-type: none;
    display: block;
    margin: 10px;
    padding: 10px;
    font-size: 20px;
    font-family: 'VT323', monospace !important;
}
#typehold{
    background: #d62267;
    height: 50px;
    bottom: 0px;
    position: absolute;
    width: 100%;
}

#typehold input[type="text"]{
    color: #fff;
    background: transparent;
    border: 0;
    border-bottom: 2px solid #fff;
    width: calc( 100% - 70px);
    position: relative;
    display: block;
    left: 10px;
    height: 35px;
    font-size: 30px;
    outline: none;
    top: 5px;
    font-family: 'VT323', monospace !important;
}

#gameBox {
    position: fixed;
    background: #589705;
    width: calc(100% - 404px);
    height: 100vh;
    overflow: hidden;
    border-right: 4px solid #000;
}

#gameWorld {
    position: absolute;
    margin: 0px;
    padding: 0px;
    top: 0px;
    left: 0px;
    width: 5400px;
    height: 2600px;
    background-image:url(/static/media/game_world.dc26315c.png);
    background-size: cover;
    -ms-interpolation-mode: nearest-neighbor;
        image-rendering: -moz-crisp-edges;
        image-rendering: pixelated;
}

#joyraj {
    width: 200px;
    height: 200px;
    position: fixed;
    top:0px;
    right: 100px;
    display: block;
    z-index: 9999 !important ;
}

/* #joyDiv {
    position: relative;
    left: 100px;
} */

.grid {
    position: absolute;
    display: block;
    background: rgba(255,255,255,0);
    color: #fff;
    font-weight: bold;
    width: 40px;
    height: 40px;
    z-index: 999;
    text-align: center;
    box-shadow:0px 0px 1px 1px #000 inset;  
    font-size: x-small;
}

.character {
    display: block;
    position: absolute;
    background-image: url(/static/media/doomer.8887584d.png);
    background-position: top left;
    background-size: cover;
    width: 88px;
    height: 240px;
    -webkit-animation: moveX 0.5s steps(2) infinite;
            animation: moveX 0.5s steps(2) infinite;
}

*{
    -ms-interpolation-mode: nearest-neighbor !important;
        image-rendering: pixelated !important;
    image-rendering: -moz-crisp-edges !important;
    image-rendering: crisp-edges !important;
}

.character.doomer {
    background-image: url(/static/media/doomer.8887584d.png);
}

.character.nerd {
    background-image: url(/static/media/nerd.caa6b080.png);
}

.character.lib {
    background-image: url(/static/media/lib.bef2b6e4.png);
}

.character.ladis {
    background-image: url(/static/media/ladis.4fc1d786.png);
}

.character.moist {
    background-image: url(/static/media/moist.e52af871.png);
}

.character.bhakt {
    background-image: url(/static/media/bhakt.9614cdc1.png);
}

@-webkit-keyframes moveX{
    from{background-position-x:0px;}
    to{background-position-x: 176px;}
  }

@keyframes moveX{
    from{background-position-x:0px;}
    to{background-position-x: 176px;}
  }

.character p b{
    font-weight: normal;
    position: relative;
    display: inline-block;
}

.character.fleft {
    transform : scaleX(-1);
}

.character.fleft p b {
    transform : scaleX(-1);
}
.character p {
    position: absolute;
    top: -50px;
    left: 50%;
    transform: translate(-50%,-50%);
    font-size: 32px;
    min-width: 100%;
    padding: 2px 10px;
    background: #e6c11d;
    color: #fff;
    text-align: center;
    font-family: 'VT323', monospace !important;
    opacity: 0;
	box-shadow: inset 0px -4px 0px 0px #E59400;
    /* margin: 0; */
}


.character p:before, .character p:after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	box-sizing: content-box;
}

.character p:before {
	top: -4px;
	left: 0;
	border-top: 4px black solid;
	border-bottom: 4px black solid;
}

.character p:after {
	left: -4px;
	top: 0;
	border-left: 4px black solid;
	border-right: 4px black solid;
}

.character:hover p {
    opacity: 1;
}



