.eightbit-btn {
	background: #2a84d8;;
	display: inline-block;
	position: relative;
	text-align: center;
	font-size: 30px;
	font-family: 'Press Start 2P', cursive;
	text-decoration: none;
	color: white;
	box-shadow: inset -4px -4px 0px 0px #1d5d99;
}

.eightbit-btn:hover {
	background: #277ccc;
	box-shadow: inset -6px -6px 0px 0px #1d5d99;
}

.eightbit-btn:active {
	box-shadow: inset 4px 4px 0px 0px #1d5d99;
}

.eightbit-btn:before, .eightbit-btn:after {
	content: '';
	position: absolute;
	width: 100%;
	height: 100%;
	box-sizing: content-box;
}

.eightbit-btn:before {
	top: -4px;
	left: 0;
	border-top: 4px black solid;
	border-bottom: 4px black solid;
}

.eightbit-btn:after {
	left: -4px;
	top: 0;
	border-left: 4px black solid;
	border-right: 4px black solid;
}

.eightbit-btn--proceed {
	background: #F7D51D;
	box-shadow: inset -4px -4px 0px 0px #E59400;
}

.eightbit-btn--proceed:hover {
	background: #F2C409;
	box-shadow: inset -6px -6px 0px 0px #E59400;
}

.eightbit-btn--proceed:active {
	box-shadow: inset 4px 4px 0px 0px #E59400;
}

